.proyectoAcuerdo{
    display: grid;
    text-align: left;
}
.titulo{
    grid-row-start: 1;
    grid-column-start: 1;
    grid-column-end: 3;
}
.codigo{
    grid-row-start: 2;
    grid-column-start: 1;
}
.comision{
    grid-row-start: 3;
    grid-column-start: 1;
}
.concejal{
    grid-row-start: 2;
    grid-column-start: 2;
    grid-row-end: 4;
    display: grid;
}
.fotoPersona{
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 3;
}
.nombrePersona{
    grid-column-start: 2;
    grid-row-start: 1;
}
.logoPartido{
    grid-column-start: 2;
    grid-row-start: 2;
    max-width: 100px;
    max-height: 80px;
}
.estados{
    grid-row-start: 4;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-end: 5;
    border: 2px black solid;
}
.informacion{
    grid-row-start: 6;
    grid-column-start: 1;
}
.alertas{
    grid-row-start: 6;
    grid-column-start: 2;
    padding-left: 10px;
}
.tags{
    color: blue;
    text-decoration: underline;
    display: inline;
}
.textoCompleto{
    font-weight: bold;
    color: blue;
    text-decoration: underline;
}
.informacion > p> h4{
    display: inline;
}


